@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;800&display=swap");
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html,
body {
  font-family: "Inter", sans-serif;
  height: 100%;
  font-size: 16px;
}

