.hero_container {
  min-height: 100vh;
  display: flex;
  padding: 3.5rem 7rem;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
  z-index: 7;
}

.wrapper {
  position: absolute;
  content: "";
  bottom: 0;
  right: 0;
  min-width: 92vw;
  min-height: 84vh;
  background-color: #f3d772;
}

/* MEDIA QUERIES */

@media screen and (max-width: 2048px), screen and (max-height: 1152px) {
  .nav_list {
    padding-top: 0.5rem;
  }
  .guy {
    margin-top: 1.8rem;
  }
}

@media screen and (max-width: 1920px), screen and (max-height: 1080px) {
  .guy {
    margin-top: 2rem;
  }
}
@media screen and (max-width: 1728px), screen and (max-height: 1117px) {
  .guy {
    margin-top: 2rem;
  }
}
