.nav_list {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}

.nav_list__item {
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 800;
  color: #000;
  margin-left: 4rem;
  padding: 0.5rem;
  transition: 0.3s ease-in-out;
}

.nav_list__item:nth-child(1):hover,
.nav_list__item:nth-child(3):hover {
  background-color: #dfb4fe;
}

.nav_list__item:nth-child(2):hover {
  background-color: #f3d772;
}
