.title_container {
  position: absolute;
  top: 7%;
  right: 10%;
  z-index: 1000;
}

.title_container__info h1 {
  display: block;
  font-size: 3rem;
  font-weight: 800;
  margin-bottom: 0.5rem;
}
.title_container__description {
  font-size: 1.2rem;
  font-weight: 400;
  text-transform: uppercase;
  display: inline-block;
  max-width: 450px;
  line-height: 2rem;
}

@media screen and (max-width: 2048px), screen and (max-height: 1152px) {
  .title_container {
    position: absolute;
    top: 7%;
    right: 13%;
    z-index: 1000;
  }
}
