.socials_list {
  display: flex;
  list-style: none;
  background-color: #dfb4fe;
  max-width: 35%;
  position: absolute;
  margin: 0 auto;
  top: 0;
  left: 0;
  right: 0;
  align-items: center;
  justify-content: center;
  padding: 2rem 0rem 30rem 0rem;
}

.socials_wrapper {
  display: flex;
  flex-direction: column;
  height: 50vh;
  justify-content: space-between;
}

.socials_list__item {
  margin-right: 1.875rem;
  padding-bottom: 0.3rem;
  transition: 0.3s ease-in-out;
}

.socials_list__item:hover {
  box-shadow: 0px 2px;
}

.socials_list__item:last-child {
  margin-right: 0rem;
}

.socials_list__item a {
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 800;
  color: #000;
}

.guy {
  z-index: 100;
}

.guy img {
  height: 100%;
  max-height: 750px;
}

@media screen and (max-width: 2048px), screen and (max-height: 1152px) {
  .guy img {
    height: 100%;
    max-height: 950px;
  }
}

@media screen and (max-width: 1728px), screen and (max-height: 1117px) {
  .guy img {
    height: 100%;
    max-height: 870px;
    margin-left: 5rem;
  }

  .nav_list__item {
    margin-left: 2.5rem;
  }
}
