.logo {
  display: flex;
  flex: 3;
  font-size: 2.25rem;
  font-weight: 800;
  flex-direction: column;
}

.position {
  font-size: 0.875rem;
  font-weight: 400;
}

@media screen and (max-width: 1728px), screen and (max-height: 1117px) {
  .logo {
    display: inline-block;
    max-width: 300px;
    width: 100%;
  }
  .position {
    display: block;
  }
}
